import BasketDoDriver from './_base';
import { format, DOM } from '../../../utils';

const { elemToFloat } = format;
const { extractElement } = DOM;

const BASE_URL = 'https://www.designtoscano.com';
const CART_URL = `${BASE_URL}/basket.do`;

/**
Driver behavior description:
+ Existing code: yes 
+ Remove code: no
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented:  no
+ Valid codes can be replaced by another valid code: yes
+ Pages, where extension works: cart
+ Is driver SPA: no
+ Gather codes on site pages: no
+ Special case about the driver works
  - maxCoupons: 7 (testing time)
 */

const Driver = class Driver extends BasketDoDriver {
	constructor() {
		super();
		this.BASE_URL = BASE_URL;
		this.REQUEST_URL = CART_URL;
		this.IS_STACKABLE = false;
		this.CAN_REMOVE = false;
		this.CODE_VALUE_SELECTOR = '#sourceCode';
		this.CART_TOTAL_SELECTOR = '.ml-basket-merchandise-subtotal .ml-basket-total-value';
		this.SUBTOTAL_SELECTOR = '.ml-basket-subtotal .ml-basket-total-value';
	}

	beforeTestCodes() {
		const isSubtotal = document.querySelector(this.SUBTOTAL_SELECTOR);
		this.TOTAL_SELECTOR = isSubtotal ? this.SUBTOTAL_SELECTOR : this.CART_TOTAL_SELECTOR;
	}

	getExistingCodes() {
		const codes = new Set(
			[...document.querySelectorAll(this.CODE_VALUE_SELECTOR)]
				.map(element => element.defaultValue)
				.filter(Boolean),
		);
		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}

	parseTotal(html) {
		if (html) {
			const isSubtotal = extractElement(this.SUBTOTAL_SELECTOR, html);
			this.TOTAL_SELECTOR = isSubtotal ? this.SUBTOTAL_SELECTOR : this.CART_TOTAL_SELECTOR;
		}
		return elemToFloat(extractElement(this.TOTAL_SELECTOR, html));
	}
};

export default {
	name: 'Design Toscano',
	domain: 'designtoscano.com',
	stackable: false,
	Driver,
	maxCoupons: 7,
	isCartEmpty() {
		return document.querySelector('.ml-basket-empty-msg');
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL);
	},
};
