import Promise from 'bluebird';
import BasketDoDriver from './_base';
import bus from '../../bus';
import { sessionTimer } from '../../lib';
import { format, DOM, browserUtils, log } from '../../../utils';

const { getCookie, setCookie } = browserUtils;
const { elemToFloat, toFloat } = format;
const { extractElement } = DOM;

/*
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works:
    - Cart page
+ Is driver SPA: no
+ Gather codes on site pages: no
+ Special case about the driver works (if it needed).
    - maxCoupons: 4 (website works really slow)
	- sessionTimer yes (15 minutes)
	- allowed to test codes 3 times in 30 minutes
 */

const DOMAIN = 'boscovs.com';
const BASE_URL = `https://www.${DOMAIN}/shop`;
const CART_URL = `${BASE_URL}/cart.do`;

const Driver = class Driver extends BasketDoDriver {
	constructor() {
		super();
		this.BASE_URL = BASE_URL;
		this.TOTAL_SELECTOR = '#order_total';
		this.CODE_VALUE_SELECTOR = '[id^=promocode]';
		this.IS_STACKABLE = true;
		this.SHIPPING_ID = '#order_shippingTotal';
		this.SHIPPING_TAX_ID = '#order_shippingTax';
		this.TAX_ID = '#order_tax';
		this.DISABLE_TIME_MIN = 30; // 30 minutes
		sessionTimer.initialize({});
	}

	checkCondition() {
		const isCookieEnabled = window.navigator.cookieEnabled;
		if (isCookieEnabled && !getCookie('codeTestingCount')) {
			setCookie('codeTestingCount', 0, this.DISABLE_TIME_MIN);
		}
		const isNotMaxCodeTesting =
			isCookieEnabled && getCookie('codeTestingCount') ? toFloat(getCookie('codeTestingCount')) < 3 : false;

		return (
			isNotMaxCodeTesting &&
			window.location.href.startsWith(CART_URL) &&
			document.querySelector(this.TOTAL_SELECTOR)
		);
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.CODE_VALUE_SELECTOR)].map(element => element.defaultValue);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	checkCodes(codes, originalPrice = this.parseTotal()) {
		log(`Original price: ${originalPrice}`);
		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this.applyCode(code).then(html => {
				const finalPrice = this.parseTotal(html);
				const finalDiscount = originalPrice - finalPrice;
				const result = { code, finalPrice, finalDiscount, isValid: false };
				if (finalPrice < originalPrice) {
					result.isValid = true;
					workingDiscounts.push(result);
				}
				return this.removeCode(code).then(() => bus.$emit('code-checked', result));
			}),
		).then(() => {
			if (this.IS_STACKABLE) {
				return bus.$emit('stack-codes', {
					codes: workingDiscounts.map(discount => discount.code),
					discounts: workingDiscounts,
					originalPrice,
				});
			}
			return Promise.resolve();
		});
	}

	stackCodes(validationInfo) {
		const workingDiscounts = validationInfo.discounts;
		const { originalPrice } = validationInfo;

		workingDiscounts.sort((a, b) => b.finalDiscount - a.finalDiscount);
		let currentPrice = originalPrice;
		let currentDiscount = 0;
		const successfulCodes = [];
		return Promise.each(workingDiscounts, discount =>
			this.applyCode(discount.code).then(text => {
				const finalPrice = this.parseTotal(text);
				const finalDiscount = originalPrice - finalPrice;
				bus.$emit('code-stacked', {
					code: discount.code,
					finalPrice,
					finalDiscount,
				});
				if (finalPrice < currentPrice) {
					successfulCodes.push(discount.code);
					currentDiscount = finalDiscount;
					currentPrice = finalPrice;
					return Promise.resolve();
				}
				return this.removeCode(discount.code);
			}),
		).then(() => {
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
			});
			return Promise.resolve();
		});
	}

	beforeApplyCodes() {
		setCookie('codeTestingCount', toFloat(getCookie('codeTestingCount')) + 1, this.DISABLE_TIME_MIN);
	}

	getPreTaxShippingTotal() {
		return this.getUpdatedCart().then(responseHTML => {
			const total = this.parseTotal(responseHTML);
			const shipping = elemToFloat(extractElement(this.SHIPPING_ID, responseHTML));
			// shipping tax is also included into the total
			const shippingTax = elemToFloat(extractElement(this.SHIPPING_TAX_ID, responseHTML));
			const tax = elemToFloat(extractElement(this.TAX_ID, responseHTML));

			return total - shipping - shippingTax - tax;
		});
	}

	_urlSearchParams(code, isApplying) {
		const params = new URLSearchParams();
		params.set('couponCode', code);
		const url = `${BASE_URL}/${isApplying ? 'add-coupon.do' : 'remove-coupon.do'}`;
		return {
			formData: params,
			url,
		};
	}

	applyCode(code) {
		const requestParams = this._urlSearchParams(code, true);
		return this._manageRequest(requestParams, true);
	}

	removeCode(code) {
		if (!this.CAN_REMOVE) {
			return Promise.resolve();
		}
		const requestParams = this._urlSearchParams(code, false);
		return this._manageRequest(requestParams, false);
	}

	_manageRequest(requestParams, redirect) {
		return fetch(`${requestParams.url}?${requestParams.formData}`, {
			method: 'GET',
			credentials: 'include',
			redirect: redirect ? 'follow' : 'manual',
		}).then(response => response.text());
	}

	getUpdatedCart() {
		return fetch(CART_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: "Boscov's",
	domain: 'boscovs.com',
	stackable: true,
	maxCoupons: 4,
	isSinglePageApp: true,
	Driver,
};
