import BasketDoDriver from './_base';
import { extractElement } from '../../../utils/DOM';
import { toFloat } from '../../../utils/format';

const BASE_URL = 'https://www.chadwicks.com';
const CART_URL = `${BASE_URL}/basket.do`;
const CHECKOUT_URL = `${BASE_URL}/checkout`;

const IS_CHECKOUT_PAGE = window.location.href.startsWith(CHECKOUT_URL);
const IS_CART_PAGE = window.location.href.startsWith(CART_URL);

/** Behavior description:
+ Existing code: yes
+ Remove code: no
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: no
+ Codes can be replaced by another code: yes
+ Manually apply code: no
+ Pages, where extension works: Cart, Checkout(payment step)
+ Is driver SPA: yes
+ Gather codes on site pages: no
 * */

const Driver = class Driver extends BasketDoDriver {
	constructor() {
		super();
		this.BASE_URL = BASE_URL;
		this.TOTAL_SELECTOR = '#EstTaxShipItemTable_estBasketTotal, .ml-summary-total .ml-accordion-summary-item-value';
		this.APPLIED_CODE_SELECTOR = '#sourceCode';
		this.APPLIED_CODE_CONTAINER_SELECTOR = '.ml-summary-group-value:last-child';
		this.EMPTY_CART_SELECTOR = '.ml-basket-empty-msg';
		this.PAYMENT_STEP_SELECTOR = '.ml-payment-wrapper';
		this.REQUEST_URL = `${this.BASE_URL}/basket.do`;
		this.CAN_REMOVE = false;
		this.IS_STACKABLE = false;
		this.EMPTY_CART_TEXT = /bag is empty/;
		this.PROMO_CODE_REG = /promo code: (\w+)/i;

		this.SHIPPING_SELECTOR = '#EstTaxShipItemTable_discountedShippingTotal';
		this.TAX_SELECTOR = '#EstTaxShipItemTable_taxTotal';
	}

	// can get existing code but not often
	getExistingCodes() {
		let codes = [];
		if (IS_CHECKOUT_PAGE) {
			codes = [...document.querySelectorAll(this.APPLIED_CODE_CONTAINER_SELECTOR)]
				.filter(el => el.innerText.match(this.PROMO_CODE_REG))
				.map(el => el.innerText.match(this.PROMO_CODE_REG)[1]);
		} else {
			codes = [document.querySelector(this.APPLIED_CODE_SELECTOR)]
				.filter(el => el.defaultValue)
				.map(el => el.defaultValue);
		}

		return {
			codes,
			codeCount: codes.length,
		};
	}

	checkCondition() {
		const emptyCartElement = document.querySelector(this.EMPTY_CART_SELECTOR);

		const isPromoInput = Boolean(document.querySelector(this.APPLIED_CODE_SELECTOR));
		const isTotal = Boolean(document.querySelector(this.TOTAL_SELECTOR));
		const isPromoStep = (document.querySelector(this.PAYMENT_STEP_SELECTOR) || {}).offsetParent;
		const isCartEmpty = Boolean(emptyCartElement && emptyCartElement.innerText.match(this.EMPTY_CART_TEXT));

		const currentState =
			(isTotal && isPromoStep && isPromoInput && IS_CHECKOUT_PAGE) || (IS_CART_PAGE && !isCartEmpty);
		return currentState;
	}

	getPreTaxShippingTotal() {
		return this.getUpdatedCart().then(responseHTML => {
			const total = this.parseTotal(responseHTML);
			const shipping = extractElement(this.SHIPPING_SELECTOR, responseHTML);
			const tax = extractElement(this.TAX_SELECTOR, responseHTML);

			const calculatedShipping =
				!shipping || shipping.innerText.toLowerCase().includes('free') ? 0 : toFloat(shipping.innerText);
			const calculatedTax =
				!tax || tax.innerText.includes('TBD') || tax.innerText.includes('Calculate')
					? 0
					: toFloat(tax.innerText);

			return total - calculatedShipping - calculatedTax;
		});
	}
};

export default {
	name: 'Chadwicks',
	domain: 'chadwicks.com',
	isSinglePageApp: true,
	Driver,
};
