import Promise from 'bluebird';
import BaseDriver from '../base';
import bus from '../../bus';
import { format, DOM, log } from '../../../utils';

const { elemToFloat } = format;
const { extractElementFromHTML } = DOM;

export default class BasketDo extends BaseDriver {
	constructor() {
		super();
		this.CODE_VALUE_SELECTOR = null;
		this.BASE_URL = null;
		this.TOTAL_SELECTOR = '#EstTaxShipItemTable_estBasketTotal';
		this.IS_STACKABLE = true;
		this.CAN_REMOVE = true;
		this.REQUEST_URL = null;
	}

	applyCodes(codes) {
		return this.IS_STACKABLE ? Promise.resolve() : this.applyCode(codes[0]);
	}

	checkCodes(codes, originalPrice = this.parseTotal()) {
		log(`Original price: ${originalPrice}`);
		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this.applyCode(code)
				.then(() => this.getUpdatedCart())
				.then(html => {
					const finalPrice = this.parseTotal(html);
					const finalDiscount = originalPrice - finalPrice;
					const result = { code, finalPrice, finalDiscount, isValid: false };
					if (finalPrice < originalPrice) {
						result.isValid = true;
						workingDiscounts.push(result);
					}
					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				}),
		).then(() => {
			if (this.IS_STACKABLE) {
				return bus.$emit('stack-codes', {
					codes: workingDiscounts.map(discount => discount.code),
					discounts: workingDiscounts,
					originalPrice,
				});
			}
			return Promise.resolve();
		});
	}

	stackCodes(validationInfo) {
		const workingDiscounts = validationInfo.discounts;
		const { originalPrice } = validationInfo;

		workingDiscounts.sort((a, b) => b.finalDiscount - a.finalDiscount);
		let currentPrice = originalPrice;
		let currentDiscount = 0;
		const successfulCodes = [];
		return Promise.each(workingDiscounts, discount =>
			this.applyCode(discount.code)
				.then(() => this.getUpdatedCart())
				.then(text => {
					const finalPrice = this.parseTotal(text);
					const finalDiscount = originalPrice - finalPrice;
					bus.$emit('code-stacked', {
						code: discount.code,
						finalPrice,
						finalDiscount,
					});
					if (finalPrice < currentPrice) {
						successfulCodes.push(discount.code);
						currentDiscount = finalDiscount;
						currentPrice = finalPrice;
						return Promise.resolve();
					}
					return this.removeCode(discount.code);
				}),
		).then(() => {
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
			});
			return Promise.resolve();
		});
	}

	getExistingCodes() {
		const codes = new Set(
			[...document.querySelectorAll(this.CODE_VALUE_SELECTOR)].map(element => element.innerText.trim()),
		);
		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}

	removeCodes(codes) {
		return Promise.each(codes, code => this.removeCode(code))
			.then(() => this.getUpdatedCart())
			.then(html => this.parseTotal(html));
	}

	_urlSearchParams(code, isApplying) {
		const params = new URLSearchParams();
		const queryParams = new URLSearchParams();
		queryParams.set('r', Math.random());
		params.set('sourceCode', code);
		queryParams.set('method', isApplying ? 'applySourceCode' : 'removeSourceCode');
		return {
			formData: params,
			queryStringParameters: queryParams,
		};
	}

	_manageRequest(requestParams) {
		if (this.REQUEST_URL === null) {
			this.REQUEST_URL = `${this.BASE_URL}/basket.do`;
		}
		return fetch(`${this.REQUEST_URL}?${requestParams.queryStringParameters.toString()}`, {
			method: 'POST',
			credentials: 'include',
			body: requestParams.formData,
		});
	}

	applyCode(code) {
		const requestParams = this._urlSearchParams(code, true);
		return this._manageRequest(requestParams);
	}

	removeCode(code) {
		if (!this.CAN_REMOVE) {
			return Promise.resolve();
		}
		const requestParams = this._urlSearchParams(code, false);
		return this._manageRequest(requestParams);
	}

	getUpdatedCart() {
		return fetch(this.REQUEST_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	parseTotal(html) {
		let total = null;
		if (html) {
			total = elemToFloat(extractElementFromHTML(html, this.TOTAL_SELECTOR));
		} else {
			total = elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
		}
		return total;
	}
}
