import Promise from 'bluebird';
import BasketDoDriver from './_base';
import bus from '../../bus';
import { DOM, sendXhr, format } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;

const BASE_URL = 'https://www.beautybrands.com';
const CART_URL = `${BASE_URL}/basket.do`;
const CHECKOUT_URL = `${BASE_URL}/checkout/accordioncheckout.do`;

/*
Driver behavior description:
    Existing code: yes
    Remove code: no
    Stackable: no
    Free gift support: no
    isValid implemented: yes
    Manually apply code: no
Optional:
    Show message about free shipping on the cart page (without discount info): no
    Is driver SPA and add the condition to show popup: yes
    Gather codes on site pages: no
*/

const Driver = class Driver extends BasketDoDriver {
	constructor() {
		super();
		this.BASE_URL = BASE_URL;
		this.TOTAL_SELECTOR = '#EstTaxShipItemTable_estBasketTotal, .ml-summary-total .ml-accordion-summary-item-value';
		this.APPLIED_CODE_SELECTOR = '.couponCodeField';

		this.PAYMENT_ELEM_SELECTOR = '.ml-accordion-steps li:nth-child(5)';
		this.PAYMENT_STEP_SELECTOR = 'ml-accordion-step-accessible';

		this.PLACE_ORDER_ELEM_SELECTOR = '.ml-accordion-steps li:nth-child(6)';
		this.PLACE_ORDER_STEP_SELECTOR = 'ml-accordion-step-accessible';

		this.SHIPPING_SELECTOR = '#EstTaxShipItemTable_discountedShippingTotal';
		this.TAX_SELECTOR = '#EstTaxShipItemTable_taxTotal';

		this.EMPTY_CART_SELECTOR = '.globalCartEmpty';

		this.IS_STACKABLE = false;
		this.CAN_REMOVE = false;

		this.REQUEST_URL = CART_URL;
		this.queryParamsForValidate = new URLSearchParams();
		this.queryParamsForValidate.set('method', 'validateSourceCode');
	}

	checkCondition() {
		const emptyCart = !!document.querySelector(this.EMPTY_CART_SELECTOR);
		const onCartPage = window.location.href.startsWith(CART_URL);
		const onCheckoutPage = window.location.href.startsWith(CHECKOUT_URL);
		const paymentElem = document.querySelector(this.PAYMENT_ELEM_SELECTOR);
		const isPaymentStep = paymentElem ? paymentElem.classList.contains(this.PAYMENT_STEP_SELECTOR) : false;
		const placeOrderElem = document.querySelector(this.PLACE_ORDER_ELEM_SELECTOR);
		const isPlaceOrderStep = placeOrderElem
			? placeOrderElem.classList.contains(this.PLACE_ORDER_STEP_SELECTOR)
			: false;
		const currentState = (!emptyCart && onCartPage) || (onCheckoutPage && isPaymentStep && !isPlaceOrderStep);
		return currentState;
	}

	getExistingCodes() {
		const codes = [];
		return Promise.resolve()
			.then(() => {
				if (!window.location.href.startsWith(CART_URL)) {
					return this.getUpdatedCart();
				}
				return Promise.resolve();
			})
			.then(html => {
				const codeValue = extractElement(this.APPLIED_CODE_SELECTOR, html).getAttribute('value');
				if (codeValue && codeValue !== ' ') {
					codes.push(codeValue);
				}
				return {
					codes,
					codeCount: codes.length,
				};
			});
	}

	checkCodes(codes, originalPrice = this.parseTotal()) {
		const workingDiscounts = [];
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: false,
			};
			return this.applyCode(code)
				.then(response => (response.status === 200 ? this.getUpdatedCart() : false))
				.then(html => {
					if (!html) {
						return bus.$emit('code-checked', result);
					}
					result.finalPrice = this.parseTotal(html);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.isValid = true;
					if (result.finalPrice < originalPrice) {
						workingDiscounts.push(result);
					}
					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				});
		});
	}

	applyCodes(codes) {
		return super.applyCodes(codes);
	}

	applyCode(code) {
		return this._getValidateCode(code).then(() => super.applyCode(code));
	}

	getPreTaxShippingTotal() {
		return this.getUpdatedCart().then(responseHTML => {
			const shippingEl = extractElement(this.SHIPPING_SELECTOR, responseHTML);
			const shipping = shippingEl ? elemToFloat(shippingEl) || 0 : 0;
			const taxEl = extractElement(this.TAX_SELECTOR, responseHTML);
			const tax = taxEl ? elemToFloat(taxEl) || 0 : 0;
			return this.parseTotal(responseHTML) - shipping - tax;
		});
	}

	// need for replacements applying
	_getValidateCode(code) {
		this.queryParamsForValidate.set('sourceCode', code);
		return sendXhr(`${CART_URL}?${this.queryParamsForValidate.toString()}`, {
			method: 'GET',
			credentials: 'include',
		});
	}
};

export default {
	name: 'Beauty Brands',
	domain: 'beautybrands.com',
	isSinglePageApp: true,
	Driver,
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) || href.startsWith(CHECKOUT_URL);
	},
};
