import BasketDoDriver from './_base';
import { extractElement } from '../../../utils/DOM';
import { toFloat } from '../../../utils/format';

const BASE_URL = 'https://www.wilsonsleather.com';
const CART_URL = `${BASE_URL}/basket.do`;
const CHECKOUT_URL = `${BASE_URL}/checkout`;

const EMPTY_ELEMENT_SELECTOR = '.ml-basket-empty-msg';

/* Behaviour description:
- We can always get the existing code on the checkout page, but not always on the cart page
- Remove code: yes
- Stackable: no
- Free gift support: no
- isValid implemented: yes
Optional:
- Show message about free shipping on the cart page (without discount info): no
- Is driver SPA and add the condition to show popup:no
- we can apply code on cart page and on checkout page
- Gather codes on site pages: yes */

const Driver = class Driver extends BasketDoDriver {
	constructor() {
		super();
		this.BASE_URL = BASE_URL;

		this.IS_STACKABLE = false;
		this.TOTAL_SELECTOR =
			'#EstTaxShipItemTable_estBasketTotal, .ml-summary-total .ml-accordion-summary-item-value, .ml-basket-subtotal .ml-basket-total-value';

		this.APPLIED_CODE_REGEX = /cart\['coupon'\] = "([A-Za-z\d]+)/;
		this.REQUEST_URL = CART_URL;

		this.MERCHANT_PROMO_BLOCK_SELECTOR = '.ml-header-global-include span';

		this.SHIPPING_SELECTOR = '#EstTaxShipItemTable_discountedShippingTotal';
		this.TAX_SELECTOR = '#EstTaxShipItemTable_taxTotal';
	}

	getMerchantCodes() {
		return [document.querySelector(this.MERCHANT_PROMO_BLOCK_SELECTOR)].filter(Boolean).map(code => code.innerText);
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll('script')]
			.map(el => el.textContent.match(this.APPLIED_CODE_REGEX))
			.filter(Boolean)
			.map(el => el[1] || null);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCode(code) {
		return fetch(`${BASE_URL}/sourcecode.do?method=reomoveSourceCode&sourceCode=${code}`, {
			method: 'GET',
			credentials: 'include',
		});
	}

	getPreTaxShippingTotal() {
		return this.getUpdatedCart().then(responseHTML => {
			const total = this.parseTotal(responseHTML);
			const shipping = extractElement(this.SHIPPING_SELECTOR, responseHTML);
			const tax = extractElement(this.TAX_SELECTOR, responseHTML);

			const calculatedShipping =
				!shipping || shipping.innerText.toLowerCase().includes('free') ? 0 : toFloat(shipping.innerText);
			const calculatedTax =
				!tax || tax.innerText.includes('TBD') || tax.innerText.includes('Calculate')
					? 0
					: toFloat(tax.innerText);

			return total - calculatedShipping - calculatedTax;
		});
	}
};

export default {
	name: "Wilson's Leather",
	domain: 'wilsonsleather.com',
	Driver,
	isCartEmpty() {
		return Boolean(document.querySelector(EMPTY_ELEMENT_SELECTOR));
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) || href.startsWith(CHECKOUT_URL);
	},
};
