import BasketDoDriver from './_base';
import bus from '../../bus';
import { format, DOM } from '../../../utils';

const { elemToFloat } = format;
const { extractElementFromHTML } = DOM;

const BASE_URL = 'https://www.donnakaran.com';
const CART_URL = `${BASE_URL}/basket.do`;
const CHECKOUT_URL = `${BASE_URL}/checkout`;

/**
 * We can apply ony one code
 * We can't remove code
 * We can get existing codes only from input.
 * But input value can be empty while code exists.
 */

const Driver = class Driver extends BasketDoDriver {
	constructor() {
		super();
		this.BASE_URL = BASE_URL;
		this.REQUEST_URL = `${BASE_URL}/basket.do`;
		this.IS_STACKABLE = false;
		this.CAN_REMOVE = false;
		this.CODE_VALUE_SELECTOR = '#sourceCode';
		this.TOTAL_SELECTOR = '.ml-accordion-summary-item-value, #EstTaxShipItemTable_estBasketTotal';
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.CODE_VALUE_SELECTOR)]
			.map(element => element.getAttribute('value'))
			.filter(Boolean);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	watchForCartUrl() {
		let lastState = false;
		setInterval(() => {
			const isOnCart = window.location.href.startsWith(CART_URL);
			let currentState = false;
			if (isOnCart) {
				currentState = !document.querySelector('.ml-basket-empty-msg');
			} else {
				const promotionsBlock = [...document.querySelectorAll('.ml-accordion-step-content')].find(value =>
					value.querySelector('#sourceCode'),
				);
				currentState = promotionsBlock ? promotionsBlock.style.height === 'auto' : false;
			}

			if (lastState !== currentState) {
				bus.$emit(currentState ? 'cart-active' : 'cart-inactive');
			}
			lastState = currentState;
		}, 500);
	}

	parseTotal(html) {
		let total = null;
		if (html) {
			total = elemToFloat(extractElementFromHTML(html, this.TOTAL_SELECTOR));
		} else {
			const elements = document.querySelectorAll(this.TOTAL_SELECTOR);
			total = elemToFloat(elements[elements.length - 1]);
		}
		return total;
	}
};

export default {
	name: 'Donna Karan Cashmere Mist',
	domain: 'donnakaran.com',
	isSinglePageApp: true,
	Driver,
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) || href.startsWith(CHECKOUT_URL);
	},
};
