import Promise from 'bluebird';
import BasketDoDriver from './_base';
import bus from '../../bus';
import { format, DOM } from '../../../utils';

const { elemToFloat, toFloat } = format;
const { extractElement } = DOM;

/* Behaviour
 * Can apply only one code
 * Can't remove codes
 */

const BASE_URL = 'https://www.chasing-fireflies.com';
const CART_URL = `${BASE_URL}/basket.do`;
const CHECKOUT_URL = `${BASE_URL}/checkout/accordioncheckout.do`;

const Driver = class Driver extends BasketDoDriver {
	constructor() {
		super();
		this.BASE_URL = BASE_URL;
		this.CODE_INPUT_SELECTOR = '#sourceCode';
		this.IS_CHECKOUT_URL = window.location.href.startsWith(CHECKOUT_URL);
		this.TOTAL_SELECTOR =
			'.ml-summary-total .ml-accordion-summary-item-value:last-child, #EstTaxShipItemTable_estBasketTotal';
		this.IS_STACKABLE = false;
		this.CAN_REMOVE = false;
		this.GET_UPDATED_CART_URL = this.IS_CHECKOUT_URL ? `${CHECKOUT_URL}?method=initData` : CART_URL;
		this.REQUEST_URL = CART_URL;
		this.EMPTY_CART_SELECTOR = '.ml-basket-empty-msg';
		this.PAYMENT_STEP_ELEMENT = 'ml-accordion-step-collapsed';

		this.paymentStepElem = document.querySelector('.ml-accordion-steps li:nth-child(4)');
	}

	checkCondition() {
		const emptyCart = document.querySelector(this.EMPTY_CART_SELECTOR);
		const isOnCart = window.location.href.startsWith(CART_URL);
		const isOnCheckout = window.location.href.startsWith(CHECKOUT_URL);
		const paymentStep = this.IS_CHECKOUT_URL
			? !this.paymentStepElem.classList.contains(this.PAYMENT_STEP_ELEMENT)
			: false;
		const currentState = !emptyCart && (isOnCart || (isOnCheckout && paymentStep));
		return currentState;
	}

	getExistingCodes() {
		const codes = [document.querySelector(this.CODE_INPUT_SELECTOR).getAttribute('value')].filter(Boolean);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	checkCodes(codes, originalPrice = this.parseTotal()) {
		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this.applyCode(code)
				.then(() => this.getUpdatedCart())
				.then(html => {
					const result = {
						code,
						finalPrice: originalPrice,
						finalDiscount: 0,
					};
					if (JSON.stringify(html).match(code)) {
						result.finalPrice = this.parseTotal(html);
						result.finalDiscount = originalPrice - result.finalPrice;
						if (result.finalPrice < originalPrice) {
							workingDiscounts.push(result);
						}
						return this.removeCode(code).then(() => bus.$emit('code-checked', result));
					}
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}),
		);
	}

	applyCodes(codes) {
		return this.applyCode(codes[0]);
	}

	getUpdatedCart() {
		return fetch(this.GET_UPDATED_CART_URL, {
			method: 'GET',
			credentials: 'include',
		})
			.then(response => response.text())
			.then(response => {
				// Return JSON if it is possible
				try {
					return JSON.parse(response);
				} catch (e) {
					return response;
				}
			});
	}

	parseTotal(data) {
		if (data && typeof data === 'object') {
			const { value } = data.targetDataForUpdate
				.find(item => item.path === 'summary')
				.data.orderTotals.find(item => item.label === 'Order Total');
			return toFloat(value);
		}
		return elemToFloat(extractElement(this.TOTAL_SELECTOR, data));
	}
};

export default {
	name: 'Chasing Fireflies',
	domain: 'chasing-fireflies.com',
	Driver,
	isSinglePageApp: true,
	isOnCartUrl() {
		return false;
	},
};
