import Promise from 'bluebird';
import BasketDoDriver from './_base';
import bus from '../../bus';
import { format, DOM, completionActions } from '../../../utils';

const { extractElement, setValue } = DOM;
const { elemToFloat } = format;

/*
Behavior description:
- can get existing codes;
- can remove codes;
- valid code can be replaced with another valid code;
- valid code can't be replaced with invalid code
- can't apply multiple codes (not stackable);
- code testing may be run on 'Cart' and 'Checkout' pages;
*/

const BASE_URL = 'https://www.herrschners.com';
const CART_URL = `${BASE_URL}/basket.do`;
const CHECKOUT_URL = `${BASE_URL}/checkout`;
const IS_ON_CART = window.location.pathname.includes('/basket.do');

const Driver = class Driver extends BasketDoDriver {
	constructor() {
		super();
		this.BASE_URL = BASE_URL;
		this.IS_STACKABLE = false;
		this.APPLIED_CODE_SELECTOR = '.basketPromoCodes .remove';
		this.CODE_INPUT_SELECTOR = '#sourceCode';
		this.APPLY_BTN_SELECTOR = '.ml-payment-source-code-apply div';
		this.PAYMENT_SECTION_SELECTOR = '.ml-accordion-steps li:nth-child(5)';
		this.CODE_REGEX = /sourceCode=(\w+)/;
		this.SUMMARY_CLASSES_SELECTOR = '.ml-accordion-summary-item-label';
		this.CHECKOUT_UPDATE_URL = 'https://www.herrschners.com/checkout/accordioncheckout.do?method=view';
	}

	watchForCartUrl() {
		let previousState = false;
		setInterval(() => {
			const isOnRightUrl = CART_URL || CHECKOUT_URL;
			const codeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
			const paymentSection = document.querySelector(this.PAYMENT_SECTION_SELECTOR);
			let isOnRightPage = null;
			if (IS_ON_CART) {
				isOnRightPage = Boolean(codeInput);
			} else {
				isOnRightPage = paymentSection
					? paymentSection.classList.contains('ml-accordion-step-accessible')
					: false;
			}
			const currentState = isOnRightUrl && isOnRightPage;
			if (currentState !== previousState) {
				bus.$emit(currentState ? 'cart-active' : 'cart-inactive');
			}
			previousState = currentState;
		}, 500);
	}

	beforeTestCodes() {
		this.lastResponse = null;
		if (!IS_ON_CART) {
			return this._getUpdatedCheckout().then(response => {
				this.lastResponse = response;
			});
		}
		return Promise.resolve();
	}

	getExistingCodes() {
		const codeElem = IS_ON_CART
			? document.querySelector(this.APPLIED_CODE_SELECTOR)
			: extractElement(this.APPLIED_CODE_SELECTOR, this.lastResponse);
		const codes = [codeElem].filter(Boolean).map(el => el.getAttribute('onclick').match(this.CODE_REGEX)[1]);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	checkCodes(codes, originalPrice = this.parseTotal()) {
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: true,
			};
			return this.applyCode(code).then(response => {
				if (this._codeAppliedCheck(response)) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}
				return this.getUpdatedCart().then(html => {
					result.finalPrice = this.parseTotal(html);
					result.finalDiscount = originalPrice - result.finalPrice;

					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			});
		});
	}

	applyCodes(codes) {
		return IS_ON_CART ? this.applyCode(codes[0]) : this._manuallyApplyCode(codes[0]);
	}

	_manuallyApplyCode(code) {
		const codeInput = document.querySelector(this.CODE_INPUT_SELECTOR);

		setValue(codeInput, code);
		document.querySelector(this.APPLY_BTN_SELECTOR).click();
		return Promise.resolve();
	}

	_codeAppliedCheck(data) {
		return data.status !== 200;
	}

	// method of finding price element because this site use angular, so many the same classes existing on the page
	_findTotalElem() {
		return [...document.querySelectorAll(this.SUMMARY_CLASSES_SELECTOR)].filter(
			el => el.textContent === 'Order Total',
		)[0].nextElementSibling;
	}

	_getUpdatedCheckout() {
		return fetch(this.CHECKOUT_UPDATE_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	parseTotal(html) {
		let total = null;
		if (html) {
			total = extractElement(this.TOTAL_SELECTOR, html);
		} else {
			total = IS_ON_CART ? document.querySelector(this.TOTAL_SELECTOR) : this._findTotalElem();
		}
		return elemToFloat(total);
	}
};

export default {
	name: 'Herrschners',
	domain: 'herrschners.com',
	Driver,
	isSinglePageApp: true,
	completionAction() {
		return IS_ON_CART ? completionActions.reload : completionActions.none;
	},
	isOnCartUrl() {
		return false;
	},
};
