import BasketDoDriver from './_base';
import { completionActions, format, DOM } from '../../../utils';
import { sessionTimer } from '../../lib';

const { elemToFloat } = format;
const { extractElement } = DOM;

const BASE_URL = 'https://www.ghbass.com';
const CART_URL = `${BASE_URL}/basket.do`;

/*
Driver behavior description:
+ Existing code: yes
+ Remove code: no
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: yes (but not with invalid)
+ Pages, where extension works: cart
+ Is driver SPA: no
+ Gather codes on site pages: no
+ Special case about the driver works (if it needed).
  - sessionTimer yes (15)
don't support the checkout page because our requests change the shipping method 
and also can't find the right parameters for a correct and complete request
  */

const Driver = class Driver extends BasketDoDriver {
	constructor() {
		super();
		this.SCRIPT_SELECTOR = '[type="text/javascript"]';
		this.EMPTY_CART_SELECTOR = '.ml-basket-empty-msg';

		this.REQUEST_URL = CART_URL;
		this.BASE_URL = BASE_URL;
		this.IS_STACKABLE = false;
		this.CAN_REMOVE = false;

		this.SHIPPING_SELECTOR = '#EstTaxShipItemTable_discountedShippingTotal';
		this.TAX_SELECTOR = '#EstTaxShipItemTable_taxTotal';

		this.CODE_REGEX = /cart\['coupon'\] = "(\w+)/;
		sessionTimer.initialize();
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.SCRIPT_SELECTOR)]
			.map(elem => elem.innerHTML.match(this.CODE_REGEX))
			.filter(Boolean)
			.map(code => code[1]);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	getPreTaxShippingTotal() {
		return this.getUpdatedCart().then(response => {
			const taxElem = extractElement(this.TAX_SELECTOR, response);
			const shippingElem = extractElement(this.SHIPPING_SELECTOR, response);
			const tax = taxElem ? elemToFloat(taxElem) || 0 : 0;
			const shipping = shippingElem ? elemToFloat(shippingElem) || 0 : 0;
			return this.parseTotal(response) - tax - shipping;
		});
	}
};

export default {
	name: 'GH Bass',
	domain: 'ghbass.com',
	completionAction: completionActions.revisit,
	Driver,
	isOnCartUrl: href => href.startsWith(CART_URL) && !document.querySelector('.ml-basket-empty-msg'),
};
